<template>
  <div>
    <h3>this is Login1</h3>

    <form @submit.stop.prevent="onSaveModal">
      <div class="text-right">
        <div class="form-group row" v-for="(v, k) in sel" :key="k">
          <template v-if="(k == pri)">
            <label class="col-sm-4 col-form-label">Id</label>
            <div class="col-sm-8">
              <input :readonly="true" :name="k" v-model="sel[k]" :class="{
                'form-control': true,
                'ko-hop-le': errors.has(k),
              }" type="text" />
            </div>
          </template>
          <template v-if="k == 'name'">
            <label class="col-sm-4 col-form-label">Name</label>
            <div class="col-sm-8">
              <input :name="k" v-model="sel[k]" autocomplete="off" :class="{
                'form-control': true,
                'ko-hop-le': errors.has(k),
              }" type="text" v-validate="'required'" />
            </div>
          </template>
          <template v-if="k == 'Username'">
            <label class="col-sm-4 col-form-label">User name</label>
            <div class="col-sm-8">
              <input :name="k" v-model="sel[k]" autocomplete="off" :class="{
                'form-control': true,
                'ko-hop-le': errors.has(k),
              }" type="text" v-validate="'required'" />
            </div>
          </template>
          <template v-if="k == 'Password'">
            <label class="col-sm-4 col-form-label">Pass</label>
            <div class="col-sm-8">
              <input :name="k" v-model="sel[k]" autocomplete="off" :class="{
                'form-control': true,
                'ko-hop-le': errors.has(k),
              }" type="text" v-validate="'required'" />
            </div>
          </template>



        </div>
        <button type="submit" class="btn mr-1">Login</button>
        <button type="button" class="btn">Cancel</button>
      </div>
    </form>
    <hr>

    <button :disabled="token == ''" @click="onLogout">Logout</button><br>
    <button @click="onGetAll">GetAll</button>



  </div>
</template>

<script>

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
  classes: true,
  fieldsBagName: 'veeFields'
});

import moment from "moment";

import vSelect from "vue-select";
Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";

import fetchClient from "@/fetchClient";


export default {
  components: {
  },
  data() {
    return {
      sel: {
        Username: 'test',
        Password: 'test'
      },
      pri: 'id',
      title: "Hi modal",
      token: ''
    }
  },
  mounted() {
    let token = (localStorage.getItem("token")) ? localStorage.getItem("token") : '';
    this.token = token;
  },
  methods: {
    onLogout() {
      localStorage.removeItem("token");
      location.reload();
    },
    onGetAll() {
      this.GetAll();
    },
    onSaveModal() {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }
        let r = JSON.parse(JSON.stringify(this.sel));
        //alert(JSON.stringify(r));
        this.Login(r);
        return;
        if (r[this.pri] == 0) {
          r[this.pri] = this.createId();//new Date().getTime();
          this.items.push(r);
          this.totalRows = this.items.length;
        } else {
          let index = this.items.findIndex(x => x[this.pri] == r[this.pri]);
          this.$set(this.items, index, r);
        }
        this.hideModal();
      });
    },

    async Login(r) {
      return await fetchClient.post(`/Users/authenticate`, r)
        .then(res => {
          //this.res = res;
          alert(JSON.stringify(res));
          //return;
          let token = res.data.token;
          localStorage.setItem("token", token);
          location.reload();           

        })
        .catch((err) => {
          alert(JSON.stringify(err.response));
          /*
            console.log(err);
            if (err.response.status == 401)
              this.res = "Chưa đăng nhập hoặc token hết hạn";
            else if (err.response.status == 403)
              this.res = "Bạn ko có quyền"
          */
          /*
            if (err.response) {
              alert(JSON.stringify(err.response));
            } else if (err.request) {
              alert(JSON.stringify(err.request));
            } else if (err.message) {
              alert(JSON.stringify(err.message));
            }
          */
        });
    },
    async GetAll() {
      return await fetchClient.get(`/Users/getall`)
        .then(res => {
          alert(JSON.stringify(res));
        })
        .catch((err) => {
          if (err.response) {
            if (err.response.status == 401)
              alert("Chưa đăng nhập hoặc token hết hạn");
            else if (err.response.status == 403)
              alert("Bạn ko có quyền");
          } else if (err.request) {
            alert(JSON.stringify(err.request));
          } else if (err.message) {
            alert(JSON.stringify(err.message));
          }
        });
    },


    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },



  }
}
</script>
